import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';

const WorkInProgress = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="work-in-progress">
      <Title> Work in Progress</Title>
      <span> Delivering Health at School and Educational Outcomes: Evidence from Brazil</span>
      <p>(with Lia Braga and <a href='https://www.rudirocha.org/'>Rudi Rocha</a>)</p>
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Project Summary{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "This paper investigates how educational technologies that use different combinations of artificial and human intelligence are incorporated into classroom instruction, and how they ultimately affect learning. We conducted a field experiment to study two technologies that allow teachers to outsource grading and feedback tasks on writing practices for the largest college admission exam in Brazil. The first technology provides instantaneous grades and feedback using only artificial intelligence. The second one uses human graders as an additional resource to enhance grading and feedback quality in aspects in which artificial intelligence arguably falls short. Both technologies significantly improved students' essay scores, and the addition of human graders did not improve effectiveness in spite of increasing perceived feedback quality. Both technologies also similarly helped teachers engage more frequently on personal discussions on essay quality with their students. Taken together, these results indicate that teachers' task composition shifted toward nonroutine activities and this helped circumvent some of the limitations of artificial intelligence. We present evidence that this happened without marked increases in teachersâ€™ workload. More generally, our results illustrate one realm where advances in artificial intelligence may increase human labor demand in analytical and interactive tasks that still remain a challenge to automation." : ''}
      </p>
    </div>
  )
}


export default WorkInProgress;
